<template>
  <scroll-view class = "album_scroll_view" scroll-y @scrolltolower = "handlelower"> 
    <!-- 
      swiper  轮播图
      1.自动轮播 autoplay
      2.指示器 indicator-dots
      3.衔接轮播  circular

      4. swiper
         默认高度 150px
      5.image
        默认高度 320px  ---基本样式中 重置了100%
        默认高度 240px
      6.计算图片高宽比例
      7.把图片的比例写到swiper标签样式中
      8.swiper-item  100%
      -->
    <!-- 轮播图开始 -->
    <view class="album_swiper">
      <swiper autoplay indicator-dots circular>
        <swiper-item v-for="item in banner" :key="item.id">
          <image :src="item.thumb"></image>
        </swiper-item>
      </swiper>
    </view>
    <!-- 轮播图结束 -->
    <!-- 列表开始 -->
    <view class="album_list">
      <navigator class="album_item"
       v-for="item in album" 
       :key="item.id"
        :url= "`/pages/album/index?id=${item.id}`">
        <view class="ablum_img">
          <!-- mode  :widthFix (宽度不变，高度自适应)  aspectFill(高度等比例拉伸，同时会把其拉伸到整张图片的大小) -->
          <image mode ="aspectFill" :src= "item.cover"></image>
        </view>
        <view class="album_info">
          <view class="album_name"> {{ item.name }} </view>
          <view class="album_desc">{{ item.desc }}</view>
          <view class="album_btn">
            <view class="album_attention">+关注</view>
          </view>
        </view>
      </navigator>
    </view>
    <!-- 列表结束 -->
  </scroll-view>
</template>

<script>
export default {
  data() {
    return {
      params: {
        limit: 30,
        order: "new",
        skip: 0,
      },
      // 轮播图数组
      banner: [],
      // 列表数组
      album: [],
      // 是否还有分页数据
      hasMore : true,
    };
  },
  mounted() {
    // 修改页面标题
    uni.setNavigationBarTitle({ title: "专辑" });
    this.getList();
  },
  methods: {
    getList() {
      this.request({
        url: "http://157.122.54.189:9088/image/v1/wallpaper/album",
        data: this.params,
      }).then((result) => {
        // console.log(result);
        if(this.banner.length ===0){
        this.banner = result.data.res.banner;
        }
        if(result.data.res.album.length===0){
          this.hasMore=flase;
          return ;
        }
        this.album =[...this.album,...result.data.res.album];
        // console.log(this.album[0].cover)
        // console.log(this.banner)
      });
    },
    handlelower(){
      if(this.hasMore){
        this.params.skip += this.params.limit;
        this.getList();
      }else{
        uni.showToast({
          title:"没有数据了",
          icon :"none"
        })
      }
    }
  },
};
</script>

<style lang="scss" scoped>

.album_scroll_view{
  height: calc(100vh - 36px);
}
.album_swiper {
  swiper {
    height: 326.1rpx;
    image {
      height: 100%;
    }
  }
}
.album_list {
  padding: 10rpx;
  .album_item {
    padding: 10rpx 0;
    display: flex;
    .ablum_img {
      flex: 1;
      padding: 10rpx;
      image {
        width: 200rpx;
        height: 200rpx;
        border-bottom: r solid #ccc;
      }
    }

    .album_info {
      flex: 2;
      padding: 0 10rpx;
      overflow:hidden;
      .album_name {
        font-size: 30rpx;
        color: #000;
        padding: 10rpx 0;
      }

      .album_desc {
        padding: 10rpx 0;
        font-size: 24rpx;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }

      .album_btn {
        padding: 10rpx;
        display: flex;
        justify-content: flex-end;
        .album_attention {
          color: $color;
          border: 1rpx solid $color;
          padding: 10rpx;
        }
      }
    }
  }
}
</style>
